.profile__name {
    font-weight: 500;
    font-size: 42px;
    line-height: 1.1;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 650px) {
    .profile__name {
        font-size: 27px;
        line-height: 1.2;
    }
}